.figureDiv {
    flex: 1;
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
}

.figureDiv img {
    border: 2px solid white;
}


.imgWrap {
    flex: 1;
    /*width: 200px;*/
    height: 100%;
    /*height: 150px;*/
    position: relative;
    display: inline-block;
    overflow: hidden;
    margin: 0;
}

imgWrap > img {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    min-height: 100%;
    min-width: 100%;
    transform: translate(-50%, -50%);
}

